<template>
    <div class="manger-view">
        <h3>SWE TimeTable</h3>
        <div id="timesheet">
            <div class="timetable">
                <table id="swe-table">
                    <thead>
                        <tr>
                            <td>Name</td>
                            <td>Time In</td>
                            <td>Time Out</td>
                            <td>Total Hours Worked</td>
                            <td>NightDiff</td>
                            <td>OT</td>
                            <!-- <td>WFH?</td> -->
                            <td>Remarks</td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="timesheet in allData" :key="timesheet.id">
                            <td>  {{ timesheet.Name }} </td>
                            <td>  {{ timesheet.Timein }} </td>
                            <td>  {{ timesheet.Timeout }} </td>
                            <td>  {{ timesheet.TotalHours }} </td>
                            <td>  {{ timesheet.NightDiff }} </td>
                            <td>  {{ timesheet.Overtime }} </td>       
                            <!-- <td>  {{ timesheet.WFH }} </td> -->
                            <td>  {{ timesheet.Remarks }} </td>
                        </tr>
                    </tbody>
                </table>
                <!-- <div v-for="timesheet in allData" :key="timesheet.id" class="timesheet">
                    {{ timesheet.Name }}
                </div> -->
            </div>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
import {mapGetters, mapActions} from 'vuex';

export default {
    name: 'manager-view',
    methods: {
        ...mapActions(['retrieveData','logTime'])
    },
    computed: mapGetters(['allData']),

    created(){
        this.retrieveData()
        this.logTime()
        //console.log(firebase.auth().currentUser.email)
    }
}
</script>

<style scoped>
    

</style>



