<template>
    <div id="swe-dashboard">
        <h3>
            Time In and Time out of SWE
        </h3>
        <div class="time-in-out">
            <button @click="timeIn" class="btn black">Time-in</button>
            <button  @click="timeOut" class="btn black">Time-out</button>
        </div>
        <div v-if="!timedIn"> </div>
        <div v-else>
            You have timed in : {{timeinDisplay}}
        </div>
        <div v-if="!timedOut"> </div>
        <div v-else>
            You have timed out : {{timeout}}
        </div>

    </div>
</template>

<script>
import firebase from 'firebase'
import db from '../firebase'
import {mapGetters, mapActions} from 'vuex';


export default {
    name: 'swe-dashbaord',
    data(){
        return {
            timedIn: false,
            timeinDisplay: '',
            timedOut: false,
            timeout: '',
            timeref: ''
        }
    },
    methods: {
        timeIn(e){
            this.timeinDisplay = new Date().toLocaleString()
            db.collection('Timsheet').add({
                Name: firebase.auth().currentUser.email,
                Timein: new Date(),
                Timeout: '',
                NightDiff: '',
                TotalHours: '',
                Overtime: '',
                Remarks: '',
            }).then(docRef => {
                this.timedIn = true
                this.timein = new Date()
                this.timeref = docRef.id
                console.log(this.timeref)
                alert('you have successfully timed in')
            })
            // console.log(firebase.auth().currentUser.email)
            e.preventDefault()
        },
        timeOut(e){
            let date1 = this.timein
            let date2 = new Date()
            
            // let hours = Math.abs(date1 - date2) / 36e5
            console.log(Math.abs( date2.getTime() - date1.getTime() ) / 36e5)
            db.collection('Timsheet').doc(this.timeref).update({
                Timeout: new Date(),
                TotalHours: Math.floor(Math.abs((date2.getTime() - date1.getTime()) / 36e5))
            }).then(()=>{
                this.timedOut = true
                this.timeout = new Date().toLocaleString()
                // console.log(doc.data())
                alert('you have successfully timed out')
            }).catch(function(error){
                alert("Error in updating", error)
            })
             
            e.preventDefault()
        }
    }
}
</script>

<style>

</style>



